<template>
    
    <div class='loginWrapper'>

        <div class="loginRegisterPageWrapper">
            
            <div class="companyHeading">

                <h1>Nutr<span class='specialI'>i</span>cijal<span class='specialK'>k</span>o<img src="../../assets/images/appleSlice.svg" alt="Orange Slice"/></h1>

            </div>

            <form-base>

                <div class="loginAndRegisterForm">

                    <div class='changeFormButtons'>

                        <button @click='switchToLogin' :class="selectedForm === 'login' ? 'activeButton' : ''">Logovanje</button>

                        <button @click='switchToRegister' :class="selectedForm === 'register' ? 'activeButton' : ''">Registracija</button>

                    </div>

                    <transition name='loginRegisterFader' mode='out-in'>

                        <div v-if="selectedForm === 'login'" class='loginRegisterFormTemplate'>

                            <input type="email" placeholder="Email ..." class='form-field' v-model="loginEmail"/>

                            <p v-if="loginEmailError" class='errorPar'>Email nije u dobrom formatu ili ne postoji u našoj bazi.</p>

                            <input type="password" placeholder="Lozinka ..." class='form-field' v-model="loginPassword"/>

                            <p v-if="loginPasswordError" class='errorPar'>Lozinka mora imati barem 8 karaktera, barem jedan broj, barem jedan specijalni karakter, barem jedno veliko slovo i barem jedno malo slovo.</p>

                            <button @click='loginCheck' class='regularButton'>Ulogujte Se</button>

                        </div>

                        <div v-else class='loginRegisterFormTemplate'>

                            <input type="email" placeholder="Ime i prezime ..." class='form-field' v-model="registerFullName"/>

                            <p v-if="registerFullNameError" class='errorPar'>Ime i prezime nije u dobrom formatu.<br/> Primer pravilnog formata : Nikola Petrović<br/>Ime i prezime ne mogu biti duži od 20 slova.</p>

                            <input type="email" placeholder="Email ..." class='form-field' v-model="registerEmail"/>

                            <p v-if="registerEmailError" class='errorPar'>Email nije u dobrom formatu ili ne postoji u našoj bazi.</p>

                            <input type="password" placeholder="Lozinka ..." class='form-field' v-model="registerPassword"/>

                            <p v-if="registerPasswordError" class='errorPar'>Lozinka mora imati barem 8 karaktera, barem jedan broj, barem jedan specijalni karakter, barem jedno veliko slovo i barem jedno malo slovo.</p>

                             <input type="password" placeholder="Ponovite lozinku ..." class='form-field' v-model="registerPasswordRepeat"/>

                            <p v-if="registerPasswordRepeatError" class='errorPar'>Lozinke se ne poklapaju.</p>

                            <button @click='registerCheck' class='regularButton'>Registrujte Se</button>

                        </div>

                    </transition>

                </div>

             </form-base>

        </div>

    </div>

    

</template>


<script>

    import FormBase from '../FormBase.vue';

    export default {
        
        components : {

            FormBase

        },

        data() {

            return { 

                selectedForm : 'login',
                loginEmail : '',
                loginPassword : '',
                registerFullName : '',
                registerEmail : '',
                registerPassword : '',
                registerPasswordRepeat : '',
                loginEmailError : false,
                loginPasswordError : false,
                registerFullNameError : false,
                registerEmailError : false,
                registerPasswordError : false,
                registerPasswordRepeatError : false

            }

        },

        methods : {

            switchToLogin() {

                this.selectedForm = 'login';

            },

            switchToRegister() {

                this.selectedForm = 'register';

            },

            loginCheck() {

                // Niz gresaka

                let loginErrorsArr = [];

                // Regexi

                let mailRegex = /^[a-z.-_\d]+@[a-z]+(\.[a-z]+){1,2}$/;

                let passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

                // Resetovanje gresaka svaki put kada pokrecemo funckiju

                this.loginEmailError = false;

                this.loginPasswordError = false;

                if(!mailRegex.test(this.loginEmail)) {

                    loginErrorsArr.push('err');

                    this.loginEmailError = true;

                }

                if(!passwordRegex.test(this.loginPassword)) {

                    loginErrorsArr.push('err');

                    this.loginPasswordError = true;

                }

                if(loginErrorsArr.length == 0) {

                    // Kod za vuex i slanje podataka na server

                }

                this.$router.push('/welcome');

            },

            registerCheck() {

                // Niz gresaka

                let registerErrorsArr = [];

                // Regexi

                let fullNameRegex = /^[A-zŠĐŽĆČ][a-zšđžćč]{2,20}\s[A-zŠĐŽĆČ][a-zšđžćč]{2,20}(\s[A-zŠĐŽĆČ][a-zšđžćč]{2,20}){0,2}$/;

                let mailRegex = /^[a-z.-_\d]+@[a-z]+(\.[a-z]+){1,2}$/;

                let passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

                // Resetovanje gresaka svaki put kada pokrecemo funckiju

                this.registerFullNameError = false;

                this.registerEmailError = false;

                this.registerPasswordError = false;

                this.registerPasswordRepeatError = false;

                if(!fullNameRegex.test(this.registerFullName)) {

                    registerErrorsArr.push('err');

                    this.registerFullNameError = true;

                }

                if(!mailRegex.test(this.registerEmail)) {

                    registerErrorsArr.push('err');

                    this.registerEmailError = true;

                }

                if(!passwordRegex.test(this.registerPassword)) {

                    registerErrorsArr.push('err');

                    this.registerPasswordError = true;

                }

                if(this.registerPasswordRepeat != this.registerPassword) {

                    registerErrorsArr.push('err');

                    this.registerPasswordRepeatError = true;

                }

                if(registerErrorsArr.length == 0) {

                    // Kod za vuex i slanje podataka na server

                }

            }

        }

    }

</script>


<style scoped lang="scss">

    $f : flex;
    $sa : space-around;
    $sb : space-between;
    $co : column;
    $c : center;
    $fw : wrap;

    .loginWrapper {

        width : 100%;
        height : 100%;
        font-family: 'Paytone One', sans-serif;

        .loginRegisterPageWrapper {

            margin : 10% 0%;
            padding : 0% 2%;
            display: $f;
            justify-content: $sa;
            align-items: $c;
            flex-direction: $co;

            .companyHeading {

                width : 35%;
                display: $f;
                justify-content : $c;
                margin-bottom: 5%;
                
                h1 {

                    color : tomato;
                    letter-spacing: 8px;
                    font-size : 3.2em;
                    display: $f;
                    align-items: $c;
                    justify-content: $c;
                    position: relative;
                    text-transform: uppercase;

                    .specialI {

                        position: relative;

                        // &::before {

                        //     content : url('../../assets/images/strawberrySlice.svg');
                        //     position: absolute;
                        //     width : 180%;
                        //     bottom : 65%;
                        //     left : -55%;

                        // }

                    }

                    // .specialK {

                    //     position: relative;

                    //     &::after {

                    //         content : url('../../assets/images/orangeSlice.svg');
                    //         position: absolute;
                    //         width : 30%;
                    //         max-width: 100%;
                    //         bottom : 5%;
                    //         left : 90%;
                    //         animation : spinIt 10s infinite;

                    //     }

                    // }

                    img {

                        height : 60px;
                        transform: rotate(-20deg);
                        //animation : spinIt 10s infinite;

                    }

                }

            }

            .loginAndRegisterForm {

                width : 100%;
                margin : 0px auto;
                display: $f;
                flex-direction: $co;
                justify-content: $c;
                align-items: $c;

                .changeFormButtons{

                    display: $f;
                    justify-content : $c;

                    button {

                        margin : 0% 4%;
                        padding : 4.5%;
                        font-family: 'Paytone One', sans-serif;
                        background-color: transparent;
                        color : whitesmoke;
                        border : none;
                        border-radius: 9px;
                        letter-spacing: 3px;
                        font-size: 1.2em;
                        outline: none;
                        cursor: pointer;
                        transition: .4s;

                        &:hover {

                            background-color: tomato;

                        }

                    }

                }

                .loginRegisterFormTemplate {

                    width : 70%;
                    display: $f;
                    flex-direction: column;
                    justify-content: $c;
                    align-items: $c;
                    margin-top: 5%;

                    button{

                        width : 45%;
                        margin-top: 5%;

                    }

                }

            }

        }

    }

    .loginRegisterFader-enter-from,
    .loginRegisterFader-leave-to {

        opacity : 0;
        //transform: translateX(30px);

    }

    .loginRegisterFader-enter-active {

        transition: all .4s ease-out;

    }

    .loginRegisterFader-leave-active {

        transition: all .4s ease-in;

    }


    .loginRegisterFader-enter-to,
    .loginRegisterFader-leave-from {

        opacity : 1;

    }

    .activeButton {

        color : whitesmoke !important;
        background-color: tomato !important;

    }



    // Animacije

    @keyframes spinIt {

        0% {

            transform: rotate(0deg);

        }

        50% {

            transform: rotate(180deg);

        }

        100% {

            transform: rotate(360deg);

        }

    }

</style>

